<template>
	<v-app id="app">
		<div id="ventas" class="myTable">
			<!--HEAD DE LA PAGINA -->
			<vue-headful title="ventas - Aranda Distribuciones"></vue-headful>

			<h2>Listado de ventas</h2>

			<b-button @click="testFetch" class="mb-4" title="Recargar" variant="light">
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<!-- ================ALTA PLANES======================== -->
			<b-button class="mb-4 ml-2"
					  v-b-modal.modal-alta
					  @click="altaventa()"
					  data-backdrop="static" 
					  data-keyboard="false"
					  title="Nuevo Pago de venta"
					  style="color: white">
				<v-icon dark> mdi-plus </v-icon>
				Nuevo venta
			</b-button>
			<b-modal no-close-on-backdrop id="modal-alta" ref="modalalta" hide-footer data-backdrop="static" data-keyboard="false">
				<template #modal-title>
					<h5 class="modal-title">Alta</h5>
				</template>
				<ventas-alta :updateTable="testFetch" />
			</b-modal>
			<!-- =========================================================== -->
			<!-- ==================================CREAR PDF================================== -->
			<b-button @click="generarPDF()"
					  id="btn_down_pdf"
					  class="mb-4 ml-2"
					  title="Generar PDF"
					  variant="danger"
					  style="color: white">
				<svg xmlns="http://www.w3.org/2000/svg"
					 width="16"
					 height="16"
					 fill="currentColor"
					 class="bi bi-file-pdf-fill"
					 viewBox="0 0 16 16">
					<path d="M5.523 10.424c.14-.082.293-.162.459-.238a7.878 7.878 0 0 1-.45.606c-.28.337-.498.516-.635.572a.266.266 0 0 1-.035.012.282.282 0 0 1-.026-.044c-.056-.11-.054-.216.04-.36.106-.165.319-.354.647-.548zm2.455-1.647c-.119.025-.237.05-.356.078a21.035 21.035 0 0 0 .5-1.05 11.96 11.96 0 0 0 .51.858c-.217.032-.436.07-.654.114zm2.525.939a3.888 3.888 0 0 1-.435-.41c.228.005.434.022.612.054.317.057.466.147.518.209a.095.095 0 0 1 .026.064.436.436 0 0 1-.06.2.307.307 0 0 1-.094.124.107.107 0 0 1-.069.015c-.09-.003-.258-.066-.498-.256zM8.278 4.97c-.04.244-.108.524-.2.829a4.86 4.86 0 0 1-.089-.346c-.076-.353-.087-.63-.046-.822.038-.177.11-.248.196-.283a.517.517 0 0 1 .145-.04c.013.03.028.092.032.198.005.122-.007.277-.038.465z" />
					<path fill-rule="evenodd"
						  d="M4 0h8a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2zm.165 11.668c.09.18.23.343.438.419.207.075.412.04.58-.03.318-.13.635-.436.926-.786.333-.401.683-.927 1.021-1.51a11.64 11.64 0 0 1 1.997-.406c.3.383.61.713.91.95.28.22.603.403.934.417a.856.856 0 0 0 .51-.138c.155-.101.27-.247.354-.416.09-.181.145-.37.138-.563a.844.844 0 0 0-.2-.518c-.226-.27-.596-.4-.96-.465a5.76 5.76 0 0 0-1.335-.05 10.954 10.954 0 0 1-.98-1.686c.25-.66.437-1.284.52-1.794.036-.218.055-.426.048-.614a1.238 1.238 0 0 0-.127-.538.7.7 0 0 0-.477-.365c-.202-.043-.41 0-.601.077-.377.15-.576.47-.651.823-.073.34-.04.736.046 1.136.088.406.238.848.43 1.295a19.707 19.707 0 0 1-1.062 2.227 7.662 7.662 0 0 0-1.482.645c-.37.22-.699.48-.897.787-.21.326-.275.714-.08 1.103z" />
				</svg>
				Generar PDF
			</b-button>
			<!-- ============================================================================== -->
			<b-button class="mb-4 ml-2"
					  variant="success"
					  id="btn_del_full"
					  title="Importar Excel"
					  style="color: white"
					  v-b-modal.modal-excel>
				Importar Excel
			</b-button>
			<b-modal no-close-on-backdrop ref="my-modal"
					 id="modal-excel"
					 hide-footer
					 title="Importar desde Excel"
					 ok-only>
				<h4>Elige un archivo para importar</h4>
				<input type="file" @change="uploadFile" ref="archivo" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" />
				<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
					Volver Atras
				</b-button>

				<b-button class="mt-3"
						  variant="success"
						  block
						  title="Importar"
						  @click="importarExcel">
					Importar
				</b-button>
			</b-modal>
			<b-button class="mb-4 ml-2"
					  variant="danger"
					  id="btn_del_full"
					  title="Eliminar todos los registros"
					  style="color: white"
					  :disabled="btn_del_full"
					  v-b-modal.modal-eliminarTodo>
				<v-icon class="mr-2" style="color: white"> mdi-delete </v-icon>
				Eliminar
			</b-button>

			<!-- ================ELIMINAR VARIOS PLANES======================== -->
			<div>
				<b-modal no-close-on-backdrop ref="my-modal"
						 id="modal-eliminarTodo"
						 hide-footer
						 title="Eliminar"
						 ok-only>
					<div class="d-block text-center" v-if="selected.length === rows">
						<h3>¿Está seguro de eliminar todos los registros?</h3>
					</div>
					<div class="d-block text-center" v-else>
						<h3 v-if="selected.length>1">¿Está seguro de eliminar {{ selected.length }} registros?</h3>
						<h3 v-else>¿Está seguro de eliminar un registro?</h3>
					</div>

					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button class="mt-3"
							  variant="danger"
							  block
							  title="Eliminar"
							  @click="delete_all_ventas()">
						Eliminar
					</b-button>
				</b-modal>
			</div>

			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group label-for="filter-input"
						  label-align-sm="right"
						  label-size="sm"
						  class="mb-0"
						  style="width: 100%; padding-bottom: 1em"
						  v-show="rows > 0">
				<b-input-group size="sm">
					<b-form-input id="filter-input"
								  v-model="filter"
								  type="search"
								  placeholder="Buscar registros"></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''">Limpiar</b-button>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>
			<!-- ======================================== -->

			<div v-if="rows > 0">
				<div v-if="selected.length > 0">
					<pre>
                        Cantidad de registros: {{ rows }} | Filas seleccionadas: {{
							selected.length
						}}</pre>
				</div>
				<div v-else>
					<pre>Cantidad de registros: {{ rows }}</pre>
				</div>
				<b-button class="mb-4 ml-2"
						  size="sm"
						  style="color: white"
						  title="Seleccionar Todo"
						  @click="seleccionar_todas"
						  :disabled="btn_select">
					Seleccionar Todo
				</b-button>
				<b-button class="mb-4 ml-2"
						  size="sm"
						  style="color: white"
						  title="Limpiar Seleccion"
						  @click="limpiar_seleccion"
						  :disabled="btn_limpiar">
					Limpiar Seleccion
				</b-button>
			</div>
			<div v-else>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>

			<section class="container">
				<b-table :fields="fields"
						 stacked
						 sortable
						 responsive
						 hover
						 :items="tabla_ventas"
						 show-empty
						 :sticky-header="false"
						 :no-border-collapse="false"
						 :per-page="perPage"
						 :current-page="currentPage"
						 ref="tablaregistros"
						 id="tablaregistros"
						 selectable
						 style="width:100%;"
						 select-mode="multi"
						 @row-selected="seleccionar_una"
						 empty-text="No hay registros cargados"
						 empty-filtered-text="No hemos encontrado registros que coincidan con lo que está buscando"
						 :filter="filter"
						 @filtered="onFiltered">


					<template #cell(selected)="{ rowSelected }">
						<template v-if="rowSelected">
							<span aria-hidden="true">&check;</span>
							<span class="sr-only">Selected</span>
						</template>
						<template v-else>
							<span aria-hidden="true">&nbsp;</span>
							<span class="sr-only">Not selected</span>
						</template>
					</template>

					<template slot="cell(id_reparto)" slot-scope="data">
						{{ data.value.split('/')[4]}}
					</template>

					<template slot="cell(id_stock)" slot-scope="data">
						{{ data.value.split('/')[4]}}
					</template>

					<template slot="cell(id_cliente)" slot-scope="data">
						{{ getCliente(data.value.split('/')[4]).nombre}}
					</template>

					<template slot="cell(gananciatotal)" slot-scope="data">
						${{ data.value}}
					</template>

					<template slot="cell(totalpagado)" slot-scope="data">
						${{ data.value}}
					</template>

					<template slot="cell(diferencia)" slot-scope="data">
						${{ data.value}}
					</template>

					<template slot="cell(action)" slot-scope="row">
						<div class="mt-3">
							<b-button-group>
								<b-button variant="info"
										  id="button-1"
										  title="Mostrar Información adicional"
										  :disabled="btn_mostrar"
										  @click="row.toggleDetails">
									<v-icon class="mr-1"> mdi-plus </v-icon>
								</b-button>

								<!--
								<b-button variant="warning"
										  id="button-2"
										  title="Editar este registro"
										  v-b-modal.modal_editar
										  @click="editarventa(row.item, row.index)"
										  :disabled="btn_editar">
									<v-icon class="mr-1"> mdi-pencil </v-icon>
								</b-button>
								-->
								<b-button variant="danger"
										  id="button-3"
										  @click="showModalinfo(row.item, row.index)"
										  title="Eliminar este registro"
										  :disabled="btn_eliminar">
									<v-icon class="mr-1"> mdi-delete </v-icon>
								</b-button>
							</b-button-group>
						</div>
					</template>
					<template #row-details="row">
						<b-card>
							<b-list-group horizontal>
								<b-list-group>
									<b-list-group-item>
										<b>Nombre del venta:</b>
										{{ row.item.fecharealizacion }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Reparto:</b>
										{{ row.item.id_reparto.split('/')[4] }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Stock:</b>
										{{ row.item.id_stock.split('/')[4] }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Cliente:</b>
										{{ getCliente(row.item.id_cliente.split('/')[4]).nombre }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Venta Total:</b>
										${{ row.item.gananciatotal }}
									</b-list-group-item>
									<b-card title="Productos: " v-if="row.item.componentes.length>0">

										<b-list-group horizontal>
											<div v-for="componente in row.item.componentes"
												 :key="componente.id">

												<b-list-group>
													<b-list-group-item>
														<b>Producto:</b>
														{{
productos.find(s=>{
                                                                    return s.id_producto == componente.producto.split('/')[4]
                                                                }).nombre
														}}
													</b-list-group-item>
													<b-list-group-item>
														<p>

															<b>Cantidad: </b>
															{{componente.cantidad}}
														</p>
													</b-list-group-item>
													<b-list-group-item>
														<p>

															<b>Precio: </b>
															${{componente.precio}}
														</p>
													</b-list-group-item>
												</b-list-group>

											</div>
										</b-list-group>
									</b-card>
									<b-list-group-item v-else>
										Sin Productos
									</b-list-group-item>

								</b-list-group>
								&nbsp;
							</b-list-group>
						</b-card>
					</template>
				</b-table>
				
					<b-container fluid>
						<b-col class="my-1">
							<b-pagination v-model="currentPage"
										  align="center"
										  pills
										  :total-rows="totalRows"
										  :per-page="perPage"
										  aria-controls="table_ventas">
							</b-pagination>
						</b-col>
					</b-container>

					<b-modal no-close-on-backdrop id="modal_editar"
							 ref="my-modaledit"
							 hide-footer
							 title="Editar"
							 ok-only>
						<ventas-update :ventas="editar" :updateTable="testFetch" />
					</b-modal>
					<!-- ================ELIMINAR UN PLAN======================== -->
					<b-modal no-close-on-backdrop id="modal_eliminar"
							 ref="my-modal"
							 hide-footer
							 title="Eliminar"
							 ok-only>
						<div class="d-block text-center">
							<h3>
								¿Esta seguro de eliminar los datos del venta '{{
								infoEliminar.venta.id_venta
								}}'?
							</h3>
						</div>
						<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
							Volver Atras
						</b-button>

						<b-button class="mt-3"
								  variant="danger"
								  block
								  title="Eliminar"
								  @click="deleteventa(infoEliminar.venta.id_venta, infoEliminar.venta)">
							Eliminar
						</b-button>
					</b-modal>
			</section>
			<aside v-show="rows > 0">
				<b-button @click="calcular" class="mb-4" title="Recargar" variant="light">
					<v-icon dark style="color: black">mdi-calculator</v-icon>
					Calcular
				</b-button>
				<div style="text-align:left;">
					<p>
						Total Vendido: ${{totalvendedo}}
					</p>
					<p>
						Total Cobrado: Mp: ${{totalmp}} + Efectivo: ${{totalefec}} = ${{totalmp + totalefec}}
					</p>
					<!--<p>
						Ganancia Total: ${{gananciatot}}
					</p>-->
				</div>
			</aside>
			<!-- ==================================CREAR PDF================================== -->

			<b-modal no-close-on-backdrop size="xl"
					 ref="modal-pdfVenta"
					 id="modal-pdfVenta"
					 hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Vista Previa</h5>
				</template>
				<ventas-listadopdf :PDFventa="ventaAPdf"></ventas-listadopdf>
			</b-modal>
			<!-- ============================================================================== -->
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://93.188.162.174:8081");
	api.pathname = "ventas/";
	//api.port = 8000;
	//api.port = 8081;

    import ventasAlta from "./VentasAlta.vue";
	import ventasUpdate from "./VentasUpdate.vue";
    import VentasListadopdf from "./VentasListadopdf.vue";
	import axios from "axios";
	import VueHtml2pdf from "vue-html2pdf";
    const XLSX = require("xlsx");
	import { APIControler } from "@/store/APIControler";
	import swal from "sweetalert";
    import Vue from 'vue';
    import VueSession from 'vue-session'
    Vue.use(VueSession)

	export default {
        components: {
            ventasAlta, ventasUpdate, VentasListadopdf,
            VueHtml2pdf },
		data() {
			return {
				tabla_ventas: [],
				productos: [],
				stockusado: {},
				stockusado2: {},
				fields: [
					{ key: "id_venta", label: "ID", sortable: true },
					{ key: "fecharealizacion", label: "Fecha", sortable: true },
					{ key: "gananciatotal", label: "Total", sortable: true },
					{ key: "id_reparto", label: "Reparto", sortable: true },
					{ key: "id_stock", label: "Stock", sortable: true },
					{ key: "id_cliente", label: "Cliente", sortable: true },
					{ key: "totalpagado", label: "Total Pagado", sortable: true },
                    { key: "diferencia", label: "Diferencia", sortable: true },
					{ key: "action", label: "Acciones", variant: "secondary" },
				],
				modal: false,
				productoelegido: {},
				dateDesde: null,
				dateHasta: null,
				ventaAPdf: {},
				op_clientes: [],
                gananciatot: 0,
                totalmp: 0,
				totalefec: 0,
				clienteelegido: {},
				totalvendedo: 0,
				editar: {},
                venta: {},
				listacli: [],
				filter: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				pageOptions: [10, 20, 40, 100, { value: 10000, text: "Todos" }],
				selected: [],
				infoEliminar: {
					id: "modal_eliminar",
					venta: -1,
				},
				btn_del_full: true,
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,
			};
		},
		computed: {
			rows() {
				return (this.totalRows = this.tabla_ventas.length);
			},
			sortOptions() {
				// Funcion para filtrar
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},

		methods: {
			
			async testFetch() {
				try {
					this.tabla_ventas = [];
					const res = await fetch(api);
					const data = await res.json();
					let id_repa = this.$route.query.id_repa;
                    for (var i = 0; i < data.results.length; i++) {
						console.log((data.results)[i]);
						if ((data.results)[i].fecharealizacion == this.fechaactual())
						{
							if (((data.results)[i]).id_reparto.split('/')[4] == id_repa)
							{
								this.tabla_ventas.push((data.results)[i])
							}
						}
                    }
					this.checkeodemodal();
				} catch (error) {
					console.log(error);
				}
			},
			calcular()
			{
                this.totalvendido();
                this.totalcobradomp();
                this.totalcobradoefec();
                this.gananciatotal();
			},
            totalvendido() {
                this.totalvendedo = 0;
                this.$refs.tablaregistros.filteredItems.forEach((venta) => {
                    this.totalvendedo += parseFloat(venta.gananciatotal);
                })
                return this.totalvendedo;
            },
            totalcobradomp() {
                this.totalmp = 0;
                this.$refs.tablaregistros.filteredItems.forEach((venta) => {
                    this.totalmp += parseFloat(venta.totalpagadomp);
                })
                return this.totalmp;
            },
            totalcobradoefec() {
                this.totalefec = 0;
                this.$refs.tablaregistros.filteredItems.forEach((venta) => {
                    this.totalefec += parseFloat(venta.totalpagado);
                })
                return this.totalefec;
            },
            gananciatotal() {
                this.gananciatot = 0;
                this.$refs.tablaregistros.filteredItems.forEach((venta) => {
                    this.gananciatot += parseFloat(venta.ganancia);
                })
                return this.gananciatot;
			},
            async actualizarStockProducto1(venta) {
                let stockfinal = this.getStock(venta.id_stock.split('/')[4])
                this.stockusado2.nombre = stockfinal.nombre;
                this.stockusado2.id_reparto = stockfinal.id_reparto;
                this.stockusado2.fechastock = stockfinal.fechastock;
                venta.componentes.forEach((produc) => {
                    let id = produc.producto.split('/')[4];
                    if (this.checkprodstock(id, venta.id_stock.split('/')[4]) == null) {
                        this.productonuevo.producto = produc.producto;
                        this.productonuevo.cantidad = 0 - parseFloat(produc.cantidad);
                        stockfinal.componentes.push(this.productonuevo);
                    } else {
                        stockfinal.componentes.forEach((compon) => {
                            if (compon.producto.split('/')[4] == id) {
                                compon.cantidad = parseFloat(compon.cantidad) + parseFloat(produc.cantidad);
                            }
                        });
                    }
                    console.log("Id del producto en venta:", id);
                });
                this.stockusado2.componentes = stockfinal.componentes;
                console.log(JSON.parse(JSON.stringify(this.stockusado2)));
                await axios
                    .put(
                        "http://93.188.162.174:8081/stocks/" +
                        stockfinal.id_stock +
                        "/",
                        this.stockusado2
                    );
            },
            checkprodstock(id_producto, id_stock) {
                let cantidad = null;
                let stockusado = this.getStock(id_stock);
                if (stockusado != {}) {
                    for (var i = 0; i < stockusado.componentes.length; i++) {
                        if (stockusado.componentes[i].producto.split('/')[4] == id_producto) {
                            cantidad = stockusado.componentes[i].cantidad;
                        }
                    }
                }
                return cantidad;
            },
            async importarExcel() {
                const data = await this.images.arrayBuffer();
                let workbook = XLSX.read(data);
                var sheet_name_list = workbook.SheetNames;
                console.log('workbook1');
                console.log(workbook);
                console.log('SheetNames');
                console.log(workbook.SheetNames);
                let VentasAPI = new APIControler();
                let datos = XLSX.utils.sheet_to_json(workbook.Sheets[sheet_name_list[0]]);
                console.log("datos");
                console.log(datos);
                VentasAPI.apiUrl.pathname = "ventas/";
                datos.forEach(element => VentasAPI.postData(element));
                this.testFetch();
			},
            uploadFile() {
                this.images = this.$refs.archivo.files[0];
                console.log(this.images);
            },
			//Funcion para mostrar el modal
			showModal() {
				this.$refs["my-modal"].show();
			},
			//Funcion para esconder el modal
			hideModal() {
				this.$refs["my-modal"].hide();
			},

			info(id) {
				alert("id: " + id);
			},
			checkeodemodal() {
				if (this.$route.query.abrir == 'true') {
                    this.$refs["modalalta"].show();
                }
			},
            fechaactual() {
                const f = new Date();
                console.log(f.toLocaleDateString());
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
			showModalinfo(item, index) {
				this.infoEliminar.venta = item;
				this.showModal();
			},
            async generarPDF() {
                if (this.tabla_ventas.length != 0) {
                    //this.btn_down_pdf=false;
                    this.ventaAPdf = this.tabla_ventas;
                    this.$refs["modal-pdfVenta"].show();
                } else {
                    //this.btn_down_pdf=true;
                    swal("Debe tener al menos 1 registro");
                }
			},
            async getStocks() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "stocks/";
                this.listastocks = await productosAPI.getData();
            },
            getStock(id) {
                let stock = this.listastocks.find(s => {
                    return s.id_stock == id
                })
                return stock;
            },
			//-----Funciones de seleccion
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},
            fechaactual() {
                const f = new Date();
                //Devuelve formato sólo de fecha pero en el formato regional actual ejemplo: 24/8/2019
                console.log(f.toLocaleDateString());

                /**
                 * Si no le convence, puede recurrir a hacer el ajuste manual
                 **/
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
			//Alta de medicamento
			altaventa() {},

			//Editar medicamento
			editarventa(item, index) {
				this.editar = item;
			},
            async actualizarStockProducto(id, stock) {
                console.log(this.getProducto(id));
                let productofinal = this.getProducto(id);
                this.productoelegido.id_producto = productofinal.id_producto
                this.productoelegido.nombre = productofinal.nombre
                this.productoelegido.tipo_producto = productofinal.tipo_producto
                this.productoelegido.letra = productofinal.letra
                this.productoelegido.numero_producto = productofinal.numero_producto
                this.productoelegido.codigo_unico = productofinal.codigo_unico
                this.productoelegido.stocktotal = parseFloat(productofinal.stocktotal) + parseFloat(stock)
                this.productoelegido.precioref = productofinal.precioref
                console.log(this.productoelegido)
                await axios
                    .put(
                        "http://93.188.162.174:8081/productos/" +
                        this.productoelegido.id_producto +
                        "/",
                        this.productoelegido
                    );
			},
            getCliente(id) {
                let cliente = this.listacli.find(s => {
                    return s.numero_cliente == id
                })
                return cliente;
			},
            async getClientes() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "clientes/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://93.188.162.174:8081/clientes/" + element.numero_cliente + "/";
                    option.text = element.nombre + ' - Saldo: $' + element.saldo;
                    console.log(option);
                    this.op_clientes.push(option);
                });
            },
            async getProductos() {
				let serviciosAPI = new APIControler();
				serviciosAPI.apiUrl.pathname = "productos/";
				this.productos= await serviciosAPI.getData()
                // .then(servicios=>{
                //     let s=[]
                //     for (d in servicios){
                //         {

                //         }
                //         s.push
                //     }
                // })


                // let option
				// this.data.forEach((element) => {
				// 	option = {};
				// 	option.value = 
				// 		"http://93.188.162.174:8081/servicios/" + element.id_servicio + "/";
				// 	option.text = element.servicio;
				// 	console.log(option);
				// 	this.op_servicios.push(option);
				// });
			},

            getProducto(id){
                if(this.productos==[]){
                    return getProductos().then(()=>{
                        return this.getProducto(id)
                    })
                }
                else{
                    return this.productos.find(s=>{
                        return s.id_producto == id
                    })
                }
            },
			//Funcion para eliminar el medicamento
            async deleteventa(id, venta) {
                this.getClientes();
                axios
                    .delete("http://93.188.162.174:8081/ventas/" + id + "/")
                    .then((datos) => {
                        console.log(venta.fecharealizacion);
                        if (venta.fecharealizacion === this.fechaactual()) {
                            this.actualizarStockProducto1(venta);
                        } else {
                            venta.componentes.forEach(element => {
                                console.log("Longitud");
                                this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
                            });
                        }
                        let saldosumado = 0;
                        saldosumado = parseFloat(venta.gananciatotal) - (parseFloat(venta.totalpagado) + parseFloat(venta.totalpagadomp))
                        if (venta.tipo_venta === 'Pollo') {
                            this.actualizarSaldoClientePollo(venta.id_cliente.split('/')[4], saldosumado)
                        } else {
                            this.actualizarSaldoClienteCongelado(venta.id_cliente.split('/')[4], saldosumado)
                        }
                        swal("Eliminacion Exitosa", " ", "success");
                        console.log(datos);
                        this.hideModal();
                    })
                    .catch((error) => {
                        swal("¡ERROR!", "Se ha detectado un problema ", "error");
                        console.log(error);
                        this.hideModal();
                    })
                    .finally(() => this.testFetch());
            },

			//Funcion para eliminar todos los medicamentos seleccionados
			async delete_all_ventas() {
                var cantidad = this.selected.length;

                try {
                    for (var i = 0; i < cantidad; i++) {
                        axios.delete(
                            "http://93.188.162.174:8081/ventas/" + this.selected[i].id_venta + "/"
                        );
                        if (this.selected.length == 0) {
                            console.log("Eliminacion Exitosa");
                            break;
                        }
                    }
                    this.hideModal();
                    swal("Eliminacion Exitosa", " ", "success");
                } catch (error) {
                    this.hideModal();
                    swal("¡ERROR!", "Se ha detectado un problema ", "error");
                    console.log(error);
                } finally {
                    this.testFetch();
                }
			},
            async actualizarSaldoClientePollo(idcli, saldosumadoporventa) {
                let clientefinal = this.getCliente(idcli)
                this.clienteelegido.numero_cliente = clientefinal.numero_cliente
                this.clienteelegido.nombre = clientefinal.nombre
                this.clienteelegido.cuitocuil = clientefinal.cuitocuil
                this.clienteelegido.calle = clientefinal.calle
                this.clienteelegido.localidad = clientefinal.localidad
                this.clienteelegido.provincia = clientefinal.provincia
                this.clienteelegido.cod_postal = clientefinal.cod_postal
                this.clienteelegido.email = clientefinal.email
                this.clienteelegido.tel_fijo = clientefinal.tel_fijo
                this.clienteelegido.tel_celular = clientefinal.tel_celular
                this.clienteelegido.categoria = clientefinal.categoria
                this.clienteelegido.saldocongelados = clientefinal.saldocongelados
                console.log(clientefinal.saldopollo)
                console.log(saldosumadoporventa)
                this.clienteelegido.saldopollo = parseFloat(clientefinal.saldopollo) - parseFloat(saldosumadoporventa)
                await axios
                    .put(
                        "http://93.188.162.174:8081/clientes/" +
                        clientefinal.numero_cliente +
                        "/",
                        this.clienteelegido
                    );
            },
            async actualizarSaldoClienteCongelado(idcli, saldosumadoporventa) {
                let clientefinal = this.getCliente(idcli)
                this.clienteelegido.numero_cliente = clientefinal.numero_cliente
                this.clienteelegido.nombre = clientefinal.nombre
                this.clienteelegido.cuitocuil = clientefinal.cuitocuil
                this.clienteelegido.calle = clientefinal.calle
                this.clienteelegido.localidad = clientefinal.localidad
                this.clienteelegido.provincia = clientefinal.provincia
                this.clienteelegido.cod_postal = clientefinal.cod_postal
                this.clienteelegido.email = clientefinal.email
                this.clienteelegido.tel_fijo = clientefinal.tel_fijo
                this.clienteelegido.tel_celular = clientefinal.tel_celular
                this.clienteelegido.categoria = clientefinal.categoria
                this.clienteelegido.saldopollo = clientefinal.saldopollo
                this.clienteelegido.saldocongelados = parseFloat(clientefinal.saldocongelados) - parseFloat(saldosumadoporventa)

                console.log(this.clienteelegido);

                await axios
                    .put(
                        "http://93.188.162.174:8081/clientes/" +
                        clientefinal.numero_cliente +
                        "/",
                        this.clienteelegido
                    );
            },
			limpiar_filtro_fecha() {
				this.dateDesde = null;
				this.dateHasta = null;
			},

			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},

		},

		beforeMount() {
            this.getProductos()
            this.getClientes();
            this.getStocks();
            this.testFetch();
		},
	};
</script>

